body {
  font-family: 'Inter', sans-serif;
}

/* Общий контейнер с градиентом фона */
.leaderboard-container {
  background: linear-gradient(135deg, #1f2937, #0f172a);
  min-height: 100vh;
  padding: 2rem;
}

/* Заголовок */
.leaderboard-title {
  text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);
}

/* Подиум */
.podium {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lower-podium {
  display: flex;
  gap: 2rem;
  margin-top: 1rem;
}

/* Стили для карточки игрока */
.player-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.player-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
}

/* Размеры карточек */
.player-card.large {
  height: 15rem; /* примерно h-60 */
  width: 12rem;  /* примерно w-48 */
}

.player-card.small {
  height: 12rem; /* примерно h-48 */
  width: 10rem;  /* примерно w-40 */
}

/* Стили для индикатора места */
.place-indicator {
  font-size: 1.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
}

.place-1 {
  color: #FBBF24; /* золотой оттенок */
}

.place-2 {
  color: #D1D5DB; /* светло-серый */
}

.place-3 {
  color: #F97316; /* оранжевый */
}

/* Название игрока */
.player-name {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

/* Стили для информационной карточки */
.info-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 1.25rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.info-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
}

/* Стили для блока зарегистрированных пользователей */
.registered-users {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 9999px;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.5);
}

/* Стили для кнопки "Назад" */
.back-button {
  position: fixed;
  bottom: 1.25rem;
  left: 1.25rem;
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  color: #fff;
  padding: 0.75rem 1.25rem;
  border: none;
  border-radius: 9999px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: transform 0.3s ease, background 0.3s ease;
}

.back-button:hover {
  transform: scale(1.05);
}

/* Анимации */
@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 rgba(255, 215, 0, 0.7);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.8);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 rgba(255, 215, 0, 0.7);
  }
}

@keyframes slide-left {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slide-right {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-pulse {
  animation: pulse 2s infinite;
}

.animate-slide-left {
  animation: slide-left 1s ease forwards;
}

.animate-slide-right {
  animation: slide-right 1s ease forwards;
}

